'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property' ;


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name DELIMITER
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/constants/path/delimiter}
*/
import DELIMITER from '@stdlib/constants-path-delimiter';
setReadOnly( ns, 'DELIMITER', DELIMITER );

/**
* @name DELIMITER_POSIX
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/constants/path/delimiter-posix}
*/
import DELIMITER_POSIX from '@stdlib/constants-path-delimiter-posix';
setReadOnly( ns, 'DELIMITER_POSIX', DELIMITER_POSIX );

/**
* @name DELIMITER_WIN32
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/constants/path/delimiter-win32}
*/
import DELIMITER_WIN32 from '@stdlib/constants-path-delimiter-win32';
setReadOnly( ns, 'DELIMITER_WIN32', DELIMITER_WIN32 );

/**
* @name SEP
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/constants/path/sep}
*/
import SEP from '@stdlib/constants-path-sep';
setReadOnly( ns, 'SEP', SEP );

/**
* @name SEP_POSIX
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/constants/path/sep-posix}
*/
import SEP_POSIX from '@stdlib/constants-path-sep-posix';
setReadOnly( ns, 'SEP_POSIX', SEP_POSIX );

/**
* @name SEP_WIN32
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/constants/path/sep-win32}
*/
import SEP_WIN32 from '@stdlib/constants-path-sep-win32';
setReadOnly( ns, 'SEP_WIN32', SEP_WIN32 );


// EXPORTS //

export default ns;
